import React from 'react';
import { RecipeFoodCard, RecipeFoodRow } from '../compontents';
import { SelectBar } from '../compontents/SelectBar';
import {
  MOCK_FOOD_DB,
  ALL_NUTRIENTS,
  db,
  highCholesterol,
} from '../constants/mock-food';
import { MACRO_SECTION_DATA } from '../constants/nutrients';

export const RecipeFoodPage = () => {
  const RECOMMENDED_NUTRIENTS = ALL_NUTRIENTS.filter((nutrient) =>
    highCholesterol.some((name) => name === nutrient.name)
  );

  const getFilteredItems = (name, type, itemsCount) => {
    const sortedItems = db
      .sort(
        (currentFood, nextFood) =>
          nextFood.nutrition[type][name] - currentFood.nutrition[type][name]
      )
      .slice(0, itemsCount);

    return sortedItems;
  };

  return (
    <div className='food-page'>
      <div className='food-page__header'>
        <SelectBar />
      </div>
      <div className='food-page__content'>
        {RECOMMENDED_NUTRIENTS.map(({ title, name, type }) => {
          return (
            <RecipeFoodRow key={name} title={title}>
              {getFilteredItems(name, type, 10).map((food) => {
                const {
                  id,
                  title,
                  imgSrc,
                  nutrition,
                  grammageInfo,
                  price,
                  variants,
                } = food;

                const nutrientValue = food.nutrition[type][name];

                return (
                  <RecipeFoodCard
                    key={id}
                    foodId={id}
                    title={title}
                    imgSrc={imgSrc}
                    nutrition={nutrition}
                    grammageInfo={grammageInfo}
                    price={price}
                    variants={variants}
                    food={food}
                    nutrientValue={nutrientValue}
                  />
                );
              })}
            </RecipeFoodRow>
          );
        })}

        {/* {MOCK_FOOD_DB.map((foodType, indexRow) => {
          return (
            <RecipeFoodRow key={indexRow} title={foodType.title}>
              {foodType.foodList.map((food, indexCard) => {
                const {
                  id,
                  title,
                  imgSrc,
                  nutrition,
                  grammageInfo,
                  price,
                  variants,
                } = food;

                return (
                  <RecipeFoodCard
                    key={indexCard}
                    foodId={id}
                    title={title}
                    imgSrc={imgSrc}
                    nutrition={nutrition}
                    grammageInfo={grammageInfo}
                    price={price}
                    variants={variants}
                    food={food}
                  />
                );
              })}
            </RecipeFoodRow>
          );
        })} */}
      </div>
    </div>
  );
};
